.circle {
  fill: red;
  opacity: 0.2;
  cursor: pointer;
  &.active,
  &:hover {
    opacity: 0.8;
  }
}
.Preview {
  position: absolute;
  top: 45%;
  left: 60%;
  width: 25%;
  height: 25%;
  // border: 2px solid green;
}
.Links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.Icon {
  width: 2vw;
  height: 2vw;
  border: 2px solid green;
  border-radius: 50%;
}

.Menu {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 1em;
  display: flex;
  flex-direction: row;
  a {
    color: rgba(255, 255, 255, 0.8);
    text-decoration: none;
    padding: 0.5em 1em;
    margin: 0.5em;
    transition: color 200ms;
    &:hover,
    &.active {
      color: rgba(255, 255, 255, 1);
    }
  }
  .Cta {
    background: orange;
    border-radius: 1em;
  }
}
.Text {
  color: white;
  text-decoration: none;
  text-align: center;
  display: block;
}
.Svg {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
}
.Video {
  width: 100%;
}
.Button {
  border: 2px solid currentColor;
  color: white;
}

.Map {
  background: rgba(0, 0, 0, 0.6);
}
.G {
  fill: rgba(255, 255, 255, 0.1);
  stroke: rgba(255, 255, 255, 0.4);
  stroke-width: 1;
}
.TemplateItaly {
  .Back {
    position: absolute;
    z-index: 1;
    top: 3%;
    left: 4%;
    text-decoration: none;
    // width: 1.5em;
    height: 1.5em;
    color: rgba(255, 255, 255, 0.8);
    transition: color 200ms;
    &:hover {
      color: rgba(255, 255, 255, 1);
    }
  }
  .Menu {
    margin: 1%;
  }
  .Text {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  .Gallery {
    position: relative;
    .Image {
      position: absolute;
      width: 100%;
      // position: absolute;
      // width:100%;
      // height: 75%;
      height: 100%;
      // height: 100%;
      // overflow: hidden;
    }
    .Navi {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      display: flex;
      flex-direction: row;
      justify-content: center;
      font-size: 50%;
      .Item {
        border: 2px solid rgba(255, 0, 0, 1);
        background: rgba(255, 0, 0, 0.8);
        width: 1em;
        height: 1em;
        border-radius: 50%;
        margin: 0.5em;
        padding: 0;
        opacity: 0.4;
        transition: opacity 200ms, transform 200ms;
        &.active {
          transform: scale(1.2);
          opacity: 0.6;
          // width: 1.5em;
          // height: 1.5em;
        }
        &:hover {
          transform: scale(1.5);
          opacity: 0.6;
          // width: 1.5em;
          // height: 1.5em;
        }
      }
    }
  }
  .Preview {
    top: 20%;
    left: 70%;
    width: 25%;
    height: 50%;
    border: 0;
    // transition: opacity 200ms;
    .Text {
      // height: auto;
    }
    .Name {
      font-size: 1.25em;
    }
    .Info {
      padding: 1em 1em 0 1em;
      font-size: 0.5em;
      line-height: 2em;
      vertical-align: top;
      text-align: left;
      display: flex;
      flex-wrap: wrap;
      .Item {
        padding: 0.5em 0;
        width: 50%;
        svg {
          position: relative;
          top: 4px;
        }
      }
    }
    .Images {
      // position: relative;
      overflow: hidden;
      height: 50%;
      margin: 10% 0;
    }
    .Button {
      padding: 2%;
    }
    .Image {
      transition: transform 10s;
    }
    .Images,
    .Button {
      color: rgba(255, 255, 255, 0.8);
      border: 1px solid currentColor;
      transition: color 200ms;
    }
    &:hover {
      .Image {
        transform: scale(1.5);
      }
      .Images,
      .Button {
        color: rgba(255, 255, 255, 1);
      }
    }
  }
  .circle {
    fill: rgba(255, 255, 255, 0.5);
    stroke: rgba(255, 255, 255, 0.2);
    stroke-width: 5;
    r: 10;
    transition: r 200ms;
    &.active {
      r: 12;
    }
    &:hover {
      r: 16;
    }
  }
  .Summary {
    position: absolute;
    top: 10%;
    left: 10%;
    right: 10%;
    bottom: 10%;
    background: rgba(255, 255, 255, 0.8);
    padding: 5%;
    line-height: 1.5em;
    overflow-y: scroll;
  }
  .dot {
    fill: rgba(255, 255, 255, 0.8);
    stroke: rgba(255, 255, 255, 0.2);
    stroke-width: 5;
    r: 5;
    transition: r 200ms;
    opacity: 0.2;
    cursor: pointer;
    &.active,
    &:hover {
      opacity: 0.8;
    }
    &.active {
      r: 6;
    }
    &:hover {
      r: 8;
    }
  }
  .Timeline {
    fill: rgba(255, 255, 255, 0.2);
  }
}
.TemplatePopup {
  .Back {
    position: absolute;
    z-index: 1;
    top: 3%;
    left: 4%;
    text-decoration: none;
    width: 1.5em;
    height: 1.5em;
    color: rgba(255, 255, 255, 0.8);
    transition: color 200ms;
    &:hover {
      color: rgba(255, 255, 255, 1);
    }
  }
  .Links {
    position: absolute;
    top: 2%;
    left: 20%;
    right: 20%;
    justify-content: center;
  }
  .Icon {
    // width: 4vw;
    // height: 4vw;
    width: 24px;
    height: 24px;
    padding: 8px;
    margin: 1vw;
    color: rgba(255, 255, 255, 1);
    border-color: currentColor;
    border-width: 1px;
    background-color: rgba(255, 255, 255, 0.3);
    transition: background-color 200ms;
    &:hover {
      background-color: rgba(255, 255, 255, 0.5);
    }
  }
  .Text {
    position: absolute;
    bottom: 0;
    text-align: left;
    padding: 2em;
    line-height: 1.5em;
    h1 {
      line-height: 1em;
    }
  }
}
